<template>
    <section>
        <Breadcrumbs :breadcrumbs="breadcrumbs"/>

        <div class="content-container" v-if="products">
            <ProductsList
                :products="paginated"
            />
        </div>
        
        <div class="no-items" v-else>
            Нет добавленных товаров
        </div>

        <Paginate
            v-if="allItems.length > 1"
            v-model="page"
            :page-count="pageCount"
            :click-handler="paginationHandler"
            :prev-class="'pagination-toggle'"
            :prev-text="'<< Назад'"
            :next-class="'pagination-toggle'"
            :next-text="'Вперед >>'"
            :container-class="'pagination'"
            :page-class="'pagination-item'"
        />
        <Loader v-if="loading"/>
    </section>
</template>

<script>
import Breadcrumbs from '@/components/admin/layout/Breadcrumbs'
import ProductsList from '@/components/admin/product/ProductsList'
import paginationMixin from '@/mixins/pagination.mixin'
import {mapGetters, mapActions} from 'vuex'

export default {
    components: {ProductsList, Breadcrumbs},
    mixins: [paginationMixin],
    metaInfo(){
        return {
            title: this.$title('Товары')
        }
    },
    data: () => ({
        loading: true,
        breadcrumbs: {
            title: 'Список товаров',
            current: 'Список товаров'
        }
    }),
    async mounted(){
        await this.fetchProducts()
        this.setupPagination(this.products)
        this.loading = false 
    },
    computed: {
        ...mapGetters(['products']),
        error(){
            return this.$store.getters.error
        },
        success(){
            return this.$store.getters.success
        }
    },
    watch: {
        error(msg){
            this.$error(msg)           
        },
        success(msg){
            this.$success(msg) 
        }
    },
    methods: {
        ...mapActions(['fetchProducts'])
    }    
}
</script>