<template>
    <table class="standart-table">
        <thead>
            <tr class="standart-table__row">
                <th
                    class="standart-table__header"
                    v-for="th of tableHeaders"
                    :key="th"
                >
                    {{th}}
                </th>                
            </tr>  
        </thead>

        <tbody class="standart-table__body">
            <ProductsListItem
                v-for="prod of products"
                :key="prod.id"
                :prod="prod"
            />
        </tbody>
    </table>
</template>
<script>
import ProductsListItem from './ProductsListItem'

export default {    
    components: {
        ProductsListItem
    },
    props: ['products'],
    data: () => ({
        tableHeaders: ['ID', 'Наименование', 'Цена', 'Статус', 'Действие']
    })
}
</script>