<template>
    <tr :class="'standart-table__row ' + prodStatus">
        <td class="standart-table__cell">{{prod.id}}</td>

        <td class="standart-table__cell">
            <router-link
                class="standart-table__link"
                title="Редактировать"
                :to="{name: 'admin_edit_product', params: {id: prod.id}}"
            >
                {{prod.title}}
            </router-link>
        </td>

        <td class="standart-table__cell">{{prod.price}}</td>

        <td class="standart-table__cell" :class="prodStatus">{{prodStatus}}</td>

        <td class="standart-table__cell">
            <router-link
                class="standart-table__link--view"
                title="Редактировать"
                :to="{name: 'admin_edit_product', params: {id: prod.id}}"
            >
                <i class="standart-table__icon fas fa-eye"></i>
            </router-link>

            <button class="standart-table__btn--change-status" title="Изменить статус" type="button"
                @click="changeStatus({id: prod.key, status: !prod.status})"
            >
                <i class="standart-table__icon fas fa-times"></i>
            </button>

            <button class="standart-table__btn--delete" title="Удалить из БД" type="button"
                @click="deleteFromDB(prod.key)"
            >
                <i class="standart-table__icon fas fa-times"></i>
            </button>
        </td>
    </tr>
</template>

<script>
import {mapActions} from 'vuex'

export default {
    props: ['prod'],
    computed: {
        prodStatus(){
            return this.prod.status ? 'On' : 'Off'
        } 
    },
    methods: {
        ...mapActions(['deleteFromDB', 'changeStatus'])                     
    }
}
</script>