export default{
    data(){
       return {
        page: +this.$route.query.page || 1,
        pageSize: 5,
        pageCount: 0,
        allItems: [],
        paginated: []}
    },
    methods: {
        setupPagination(allItems){
            let tempArr = []

            for (let i = 0; i < allItems.length; i += this.pageSize) {
                tempArr.push(allItems.slice(i, i + this.pageSize))
            }

            this.allItems = tempArr
            this.pageCount = this.allItems.length
            this.paginated = this.allItems[this.page - 1] || this.allItems[0]
        },
        paginationHandler(page){
            this.$router.push(`${this.$route.path}?page=${page}`)
            this.paginated = this.allItems[page - 1] || this.allItems[0]
        }
    }
}